// pages/inventory.js
import { useState, useEffect } from "react";
import Layout from "../components/Layout";
import Modal from "../components/Modal";
import { useAuth } from "../context/AuthContext";
import WarningIndicator from "../components/WarningIndicator";

function getWarnings(p) {
  const warnings = [];
  if (p.quantity <= 0) warnings.push("out_of_stock");
  else if (p.quantity <= (p.minQty || 5)) warnings.push("low_stock");

  if (p.expiryDate) {
    const diffDays =
      (new Date(p.expiryDate).getTime() - new Date().getTime()) /
      (1000 * 60 * 60 * 24);
    if (diffDays < 0) warnings.push("expired");
    else if (diffDays <= 60) warnings.push("near_expiry");
  }
  return warnings;
}

export default function InventoryPage() {
  const { user, hasPermission } = useAuth();

  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);

  const [showModal, setShowModal] = useState(false);
  const [selected, setSelected] = useState(null);
  const [qty, setQty] = useState("");
  const [type, setType] = useState("in");

  // -----------------------------------------------------------
  // üî• ÿ™ÿ≠ŸÖŸäŸÑ ŸÇÿßÿ¶ŸÖÿ© ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ ŸÖŸÜ API
  // -----------------------------------------------------------
  useEffect(() => {
    const fetchInventory = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/inventory");
        const data = await res.json();

        if (data.success) {
          setProducts(data.data);
        } else {
          alert("ŸÅÿ¥ŸÑ ÿ™ÿ≠ŸÖŸäŸÑ ÿ®ŸäÿßŸÜÿßÿ™ ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ");
        }
      } catch (err) {
        console.error(err);
        alert("ÿÆÿ∑ÿ£ ŸÅŸä ÿßŸÑÿßÿ™ÿµÿßŸÑ ÿ®ÿßŸÑÿ≥Ÿäÿ±ŸÅÿ±");
      } finally {
        setLoading(false);
      }
    };

    fetchInventory();
  }, []);

  // -----------------------------------------------------------
  // üîÑ ŸÅÿ™ÿ≠ ŸÜÿßŸÅÿ∞ÿ© ÿ™ÿπÿØŸäŸÑ ÿßŸÑŸÉŸÖŸäÿ©
  // -----------------------------------------------------------
  const openModal = (p) => {
    setSelected(p);
    setQty("");
    setType("in");
    setShowModal(true);
  };

  // -----------------------------------------------------------
  // üßæ ÿ•ÿ±ÿ≥ÿßŸÑ ÿ™ÿ≠ÿØŸäÿ´ ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ ÿ•ŸÑŸâ API
  // -----------------------------------------------------------
  const handleConfirm = async () => {
    const n = Number(qty);
    if (!n || n <= 0) {
      alert("ÿ£ÿØÿÆŸÑ ŸÉŸÖŸäÿ© ÿµÿ≠Ÿäÿ≠ÿ©");
      return;
    }

    try {
      const res = await fetch(
        `http://localhost:5000/api/inventory/${selected.id}/adjust`,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ type, quantity: n }),
        }
      );

      const data = await res.json();

      if (!data.success) {
        alert(data.message || "ÿÆÿ∑ÿ£ ÿ£ÿ´ŸÜÿßÿ° ÿ™ÿπÿØŸäŸÑ ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ");
        return;
      }

      // üî• ÿ™ÿ≠ÿØŸäÿ´ ÿßŸÑŸàÿßÿ¨Ÿáÿ©
      setProducts((prev) =>
        prev.map((p) => (p.id === selected.id ? data.data : p))
      );

      setShowModal(false);
    } catch (err) {
      console.error(err);
      alert("ÿÆÿ∑ÿ£ ŸÅŸä ÿßŸÑÿßÿ™ÿµÿßŸÑ ÿ®ÿßŸÑÿ≥Ÿäÿ±ŸÅÿ±");
    }
  };

  // -----------------------------------------------------------
  // üö´ ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©
  // -----------------------------------------------------------
  if (!hasPermission(["admin", "pharmacist"])) {
    return (
      <Layout user={user} title="ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ">
        <div dir="rtl" className="p-6 text-center text-red-600">
          ‚ö†Ô∏è ŸÑŸäÿ≥ ŸÑÿØŸäŸÉ ÿµŸÑÿßÿ≠Ÿäÿ© ŸÑÿØÿÆŸàŸÑ ÿ¥ÿßÿ¥ÿ© ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ.
        </div>
      </Layout>
    );
  }

  // -----------------------------------------------------------
  // ‚è≥ ÿ™ÿ≠ŸÖŸäŸÑ ...
  // -----------------------------------------------------------
  if (loading) {
    return (
      <Layout user={user} title="ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ">
        <div dir="rtl" className="p-6 text-center">‚è≥ ÿ¨ÿßÿ±Ÿä ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ‚Ä¶</div>
      </Layout>
    );
  }

  // -----------------------------------------------------------
  // üé® ÿßŸÑŸàÿßÿ¨Ÿáÿ©
  // -----------------------------------------------------------
  return (
    <Layout user={user} title="ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ">
      <div dir="rtl" className="space-y-6">
        
        <h1 className="text-xl font-bold text-gray-800">üè¨ ÿ•ÿØÿßÿ±ÿ© ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ</h1>

        <div className="overflow-x-auto bg-white border shadow rounded-xl">
          <table className="w-full text-sm text-right">
            <thead className="text-gray-700 bg-gray-50">
              <tr>
                <th className="p-3">ÿßŸÑŸÖŸÜÿ™ÿ¨</th>
                <th className="p-3">ÿßŸÑŸÉŸàÿØ</th>
                <th className="p-3">ÿßŸÑŸÅÿ¶ÿ©</th>
                <th className="p-3">ÿßŸÑŸÉŸÖŸäÿ©</th>
                <th className="p-3">ÿßŸÑÿ≠ÿØ ÿßŸÑÿ£ÿØŸÜŸâ</th>
                <th className="p-3">ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©</th>
                <th className="p-3 text-center">ÿ™ÿ≠ÿ∞Ÿäÿ±ÿßÿ™</th>
                <th className="p-3 text-center">ÿ•ÿ¨ÿ±ÿßÿ°ÿßÿ™</th>
              </tr>
            </thead>

            <tbody>
              {products.map((p) => {
                const warnings = getWarnings(p);
                const daysLeft = p.expiryDate
                  ? (new Date(p.expiryDate) - new Date()) /
                    (1000 * 60 * 60 * 24)
                  : null;

                return (
                  <tr key={p.id} className="border-t hover:bg-gray-50">
                    <td className="p-3">{p.name}</td>
                    <td className="p-3 text-xs text-gray-600">{p.sku}</td>
                    <td className="p-3">{p.category}</td>

                    <td
                      className={`p-3 ${
                        p.quantity <= 0
                          ? "text-red-700 font-bold"
                          : p.quantity < (p.minQty || 5)
                          ? "text-amber-600 font-semibold"
                          : ""
                      }`}
                    >
                      {p.quantity}
                    </td>

                    <td className="p-3">{p.minQty}</td>

                    <td className="p-3 text-xs">
                      {p.expiryDate
                        ? daysLeft < 0
                          ? "‚ùå ŸÖŸÜÿ™ŸáŸä"
                          : p.expiryDate
                        : "-"}
                    </td>

                    <td className="p-3 text-center">
                      <WarningIndicator warnings={warnings} />
                    </td>

                    <td className="p-3 text-center">
                      <button
                        onClick={() => openModal(p)}
                        className="px-3 py-1 text-xs text-white rounded-lg bg-sky-600 hover:bg-sky-700"
                      >
                        üîÑ ÿ™Ÿàÿ±ŸäÿØ / ÿÆÿµŸÖ
                      </button>
                    </td>
                  </tr>
                );
              })}

              {products.length === 0 && (
                <tr>
                  <td colSpan={8} className="p-4 text-center text-gray-400">
                    ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿ£ÿµŸÜÿßŸÅ ÿ≠ÿßŸÑŸäÿßŸã‚Ä¶
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>

      {showModal && selected && (
        <Modal
          title="ÿ™ÿπÿØŸäŸÑ ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ"
          onClose={() => setShowModal(false)}
          onConfirm={handleConfirm}
        >
          <div dir="rtl" className="space-y-3 text-sm">
            <p>
              ÿßŸÑŸÖŸÜÿ™ÿ¨: <strong>{selected.name}</strong>
            </p>

            <div>
              <label className="block mb-1 text-xs text-gray-500">
                ŸÜŸàÿπ ÿßŸÑÿπŸÖŸÑŸäÿ©
              </label>
              <select
                className="w-full p-2 border rounded"
                value={type}
                onChange={(e) => setType(e.target.value)}
              >
                <option value="in">‚ûï ÿ™Ÿàÿ±ŸäÿØ</option>
                <option value="out">‚ûñ ÿÆÿµŸÖ</option>
              </select>
            </div>

            <div>
              <label className="block mb-1 text-xs text-gray-500">
                ÿßŸÑŸÉŸÖŸäÿ©
              </label>
              <input
                type="number"
                className="w-full p-2 border rounded"
                placeholder="ŸÖÿ´ÿßŸÑ: 10"
                value={qty}
                onChange={(e) => setQty(e.target.value)}
              />
            </div>
          </div>
        </Modal>
      )}
    </Layout>
  );
}


























